.seats {
  width: 82%;
  margin-left: 85px;
}

.seats-title {
  font-family: "Roboto Medium";
  font-size: 30px;
  line-height: 35px;
  text-transform: uppercase;
  color: #292929;
  margin: 0 0 62px 0;
}

.seats-buttons {
  height: 183px;
  width: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  margin-bottom: 140px;
}

.seats-button {
  height: 60px;
  padding: 0 70px 0 70px;
  font-family: "Roboto Bold";
  font-size: 24px;
  text-transform: uppercase;
  color: #ffffff;
  background: #ffa800;
  border-radius: 5px;
  border: none;
  cursor: pointer;
}

.seats-button:hover {
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}

.seats-button:active {
  color: #ffa800;
  border: 1px solid #ffa800;
  background-color: #e5e5e5;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}

.seats-button:disabled {
  background: #928f94;
  color: #ffffff;
}

.order-options {
  position: relative;
  margin-bottom: 285px;
}