.footer {
  width: 100%;
  background: #2d2b2f;
  color: #e5e5e5;
}

.footer__contacts-wrapper {
  display: flex;
  flex-wrap: wrap;
}

.footer__contacts {
  padding-left: 15%;
}

.footer-title {
  font-family: "Roboto Medium";
  font-size: 30px;
  line-height: 35px;
  margin-top: 49px;
  margin-bottom: 34px;
  color: #ffffff;
}

.second-title {
  margin-top: 29px;
  margin-bottom: 28px;
}

.footer__contacts-list {
  min-width: 340px;
  list-style-type: none;
  margin: 0 0 52px;
  padding: 0 0 0 66px;
  position: relative;
}

.footer__contacts-item {
  display: flex;
  font-family: "Roboto Regular";
  font-size: 24px;
  line-height: 28px;
}

.footer__contacts-text {
  margin: 29px 0 0;
  font-family: "Roboto Regular";
  font-size: 24px;
  color: #E5E5E5;
}

.footer__contacts-icon {
  position: absolute;
  left: 0px;
  width: 30px;
  margin-top: 29px;
}

.footer__form-label {
  font-family: "Roboto Regular";
  font-size: 24px;
  line-height: 28px;
  color: #E5E5E5;
}

.input-wrapper {
  display: flex;
  height: 60px;
  margin-top: 15px;
}

.footer__form-input {
  width: 447px;
  border-radius: 3px;
  padding-left: 21px;
  font-family: "Roboto Regular";
  font-size: 24px;
}

.footer__form-input::placeholder {
  color: #e5e5e5;
}

.footer__form-button {
  font-family: "Roboto Regular";
  font-size: 24px;
  line-height: 28px;
  text-transform: uppercase;
  padding: 0 17px;
  margin-left: 33px;
  background: #2d2b2f;
  color: #e5e5e5;
  border: 1px solid #ffffff;
  border-radius: 3px;
  transition: .3s;
  cursor: pointer;
}

.footer__form-button:hover {
  border-color: transparent;
  background: #ffca62;
  color: #2d2b2f;
}

.footer__form-button:active {
  border-color: transparent;
  background-color: #ffffff;
  color: #2d2b2f;
}

.footer__subscribe {
  margin-left: 15%;
  margin-bottom: 50px;
}

.footer__subscribe-link {
  margin-right: 49px;
  fill: #ffffff;
}

.footer__subscribe-link:hover {
  border-color: transparent;
  fill: #ffca62;
}

.footer__logo-wrapper {
  border-top: 1px solid #e5e5e5;
  padding-top: 26px;
}

.content__logo-wrapper {
  display: flex;
  justify-content: space-between;
}

.footer__copyright {
  font-family: "Roboto Light";
  font-size: 24px;
  line-height: 28px;
}

.line {
  width: 100%;
  height: 2px;
  border-bottom: 1px solid #E5E5E5;
  position: absolute;
}

.footer__bottom-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 22px;
  padding-bottom: 22px;
  padding-right: 10%;
}

.footer__bottom-wrapper-text {
  font-family: "Roboto Light";
  font-size: 24px;
  line-height: 28px;
  color: #E5E5E5;
}

.footer__up-arrow {
  object-fit: contain;
  cursor: pointer;
}