.coach-info {
  display: flex;
}

.coach-info-header {
  box-sizing: border-box;
  width: 190px;
  height: 161px;
  background: #ffd98f;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 30px;
}

.coach-number {
  font-style: normal;
  font-family: "Roboto Bold";
  font-size: 72px;
  line-height: 84px;
  margin: 0;
  color: #292929;
}

.coach-title {
  font-family: "Roboto Regular";
  font-size: 24px;
  line-height: 28px;
  margin: 0;
  color: #292929;
}

.coach-info-card {
  font-family: "Roboto Regular";
  font-size: 24px;
  line-height: 28px;
  color: #000000;
}

.coach-info-card--seats {
  padding-left: 85px;
}

.coach-info-card--price {
  padding-left: 100px;
  font-family: "Roboto Bold";
}

.coach-info-card--services {
  padding-left: 100px;
}

.coach-info-title {
  font-family: "Roboto Regular";
  font-size: 18px;
  line-height: 21px;
  color: #928f94;
  margin-top: 33px;
  margin-bottom: 21px;
}

.coach-ticket-сurrency {
  height: 17px;
  margin-left: 10px;
}

.coach-info-count {
  color: #000000;
}

.coach-info-text {
  margin-bottom: 24px;
  font-family: "Roboto Regular";
  font-size: 24px;
  color: #000;
}

.coach-info-text:last-child {
  margin-bottom: 0;
}

.coach-info-text strong {
  font-family: "Roboto Bold";
  font-size: 24px;
  color: #000;
}

.coach-info-text .currency-item {
  font-family: "Roboto Bold";
  font-size: 24px;
  color: #000;
}

.coach-services {
  margin-top: 14px;
}

.service {
  width: 36px;
  height: 34.11px;
  border: 1px solid #292929;
  border-radius: 5px;
  background-color: transparent;
  background-position: center;
  background-repeat: no-repeat;
}

.service:nth-child(n + 2) {
  margin-left: 13px;
}

.service:hover {
  background-color: #ffa800;
}

.service--active {
  border-color: #fdb935;
  background-color: #fdb935;
}

.service:disabled {
  border-color: #fcdc9d;
  background-color: #fcdc9d;
}

.air-service {
  background-image: url('../../../images/tickets/options/air-service.svg');
}

.air-service:disabled {
  background-image: url('../../../images/tickets/options/air-service-disable.svg');
}

.air-service--active {
  background-image: url('../../../images/tickets/options/air-service-active.svg');
}

.wifi-service {
  background-image: url('../../../images/tickets/options/wifi-service.svg');
}

.wifi-service:disabled {
  background-image: url('../../../images/tickets/options/wifi-service-disable.svg');
}

.wifi-service--active {
  background-image: url('../../../images/tickets/options/wifi-service-active.svg');
}

.linens-service {
  background-image: url('../../../images/tickets/options/linen-service.svg');
}

.linens-service:disabled {
  background-image: url('../../../images/tickets/options/linen-service-disable.svg');
}

.linens-service--active {
  background-image: url('../../../images/tickets/options/linen-service-active.svg');
}

.food-service {
  background-image: url('../../../images/tickets/options/food-service.svg');
}

.food-service:disabled {
  background-image: url('../../../images/tickets/options/food-service-disable.svg');
}

.food-service--active {
  background-image: url('../../../images/tickets/options/food-service-active.svg');
}

.coach-seats-list {
  position: relative;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 0;
  width: 921px;
  height: 145px;
  background-repeat: no-repeat;
  background-size: cover;
}

.coach-demand {
  margin-left: auto;
  margin-right: 42px;
  width: 199px;
  padding: 16px;
  border-radius: 5px;
  background-color: #f3f3f3;
  font-size: 16px;
  line-height: 19px;
  color: #292929;
}

.coach-seats-list--platzcart {
  background-image: url('../../../images/classes/platzcart.png');
}

.coach-seats-list--kupe {
  background-image: url('../../../images/classes/kupe.png');
}

.coach-seats-list--lux {
  background-image: url('../../../images/classes/lux.png');
}

.coach-seats-list--seat {
  background-image: url('../../../images/classes/seat.png');
}

.coach-seat {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 0;
  background-color: #f2f2f2;
}

.coach-seat:hover {
  background-color: #ffa800;
}

.coach-wagon-number {
  position: absolute;
  top: 0;
  left: 41px;
  width: 33px;
  height: 25px;
  color: #fff;
  background-color: #292929;
  text-align: center;
}

.coach-seat-number {
  color: #000;
}

.coach-seat--active {
  border: 2px solid #ffa800;
}

.coach-seat:disabled {
  background-color: #cccccc;
  color: #ffffff;
}

.coach-seat--top {
  width: 27px;
  height: 30px;
}

.coach-seat--bottom {
  width: 43px;
  height: 24px;
}

.coach-seat--lux {
  width: 26px;
  height: 59px;
}

.coach-seat--single {
  width: 27px;
  height: 19px;
}

.coach-total {
  height: 81px;
}

.coach-total-text {
  margin: 0;
  padding-top: 24px;
  text-align: end;
  padding-right: 38px;
}

.coach-total-value {
  font-family: "Roboto Bold";
  font-size: 28px;
  line-height: 28px;
  padding-right: 5px;
  color: #ffa800;
}

.coach-total-сurrency {
  height: 20px;
}