.pay-form-wrapper {
  width: 100%;
}

.pay-form {
  margin-left: 89px;
  position: relative;
}

.pay-form_form {
  background: #ffffff;
  border: 1px solid #c4c4c4;
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.25);
}

.pay-form_title {
  font-family: "Roboto Regular";
  font-size: 30px;
  line-height: 107px;
  margin: 0;
  color: #292929;
  background: #f9f9f9;
  height: 107px;
  padding-left: 35px;
  border-bottom: 1px dashed #928f94;
}

.pay-form_title-repeat {
  border-top: 1px dashed #928f94;
}

.pay-form-controls {
  display: flex;
  justify-content: space-between;
  margin-top: 30px;
}

.pay-form-controls-contacts {
  flex-direction: column;
}

.pay-form_wrapper {
  padding-left: 35px;
  padding-right: 35px;
}

.pay-form_label {
  display: flex;
  flex-direction: column;
  width: 32%;
  font-family: "Roboto Regular";
  font-size: 16px;
  line-height: 19px;
  color: #292929;
}

.pay-form-controls-contacts {
  margin-bottom: 89px;
  width: 125%;
}

.pay-form_field-control {
  display: none;
}

.pay-form_label-radio::before {
  content: '';
  display: inline-block;
  vertical-align: middle;
  width: 28px;
  height: 28px;
  margin-right: 20px;
  border: 1px solid #928f94;
  border-radius: 5px;
}

.pay-form_field-control:checked~.pay-form_label-radio::before {
  border-color: #ffa800;
  background-image: url('../../../../images/form/checkbox.svg');
  background-repeat: no-repeat;
  background-position: center;
  background-size: 23px 18px;
}

.pay-form_field-control:checked~.pay-form_label-radio {
  color: #ffa800;
}

.pay-form_label-contact:last-child {
  margin-top: 38px;
}

.pay-form_field {
  box-sizing: border-box;
  height: 50px;
  border: 1px solid #928f94;
  border-radius: 5px;
  background: #ffffff;
  color: #292929;
  font-family: "Roboto Regular";
  font-size: 24px;
  line-height: 28px;
  padding-left: 13px;
  outline: none;
  margin-top: 13px;
}

.pay-form_field::placeholder {
  font-size: 16px;
  line-height: 19px;
}

.pay-form_controls-group {
  padding: 52px 35px 75px;
}

.pay-form_controls-group:last-child {
  border-top: 1px dashed #928f94;
}

.pay-form_label-radio {
  font-family: "Roboto Regular";
  font-size: 24px;
  line-height: 28px;
  color: #928f94;
  display: inline;
}

.pay-form_pay-list {
  list-style: none;
  padding: 0;
  display: flex;
  margin: 0;
}

.pay-form_pay-item {
  font-family: "Roboto Bold";
  font-size: 24px;
  line-height: 28px;
  margin-right: 120px;
  margin-top: 30px;
  margin-bottom: 0;
  color: #292929;
}

.pay-form_buttons {
  height: 183px;
  width: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  margin-bottom: 140px;
}

.pay-form_button {
  height: 60px;
  padding: 0 70px 0 70px;
  font-family: "Roboto Bold";
  font-size: 24px;
  text-transform: uppercase;
  color: #ffffff;
  background: #ffa800;
  border-radius: 5px;
  border: none;
}

.pay-form_button:hover {
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}

.pay-form_button:active {
  color: #ffa800;
  border: 1px solid #ffa800;
  background-color: #e5e5e5;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}

.pay-form_button:disabled {
  background: #928f94;
  color: #ffffff;
}